
import { Component, OnInit } from '@angular/core';
import { NavController, LoadingController, ActionSheetController, AlertController } from '@ionic/angular';
import { AppMin } from '../core';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-application-consultant1',
  templateUrl: './application-consultant1.page.html',
  styleUrls: ['./application-consultant1.page.scss'],
})
export class ApplicationConsultant1Page implements OnInit {
  header_class;
  loader;

  input_CName = "";
  input_name = "";
  input_PhoneUnmber = "";
  input_QQ = "";
  input_workYears = "";
  input_age = ""; //年龄
  input_gender = "";  //输出性别
  input_sex = ""//提交性别
  input_otherPlatforms = "";  //其他平台
  checkGender = false;


  constructor(public navCtrl: NavController, public Router: Router, private alertCtrl: AlertController, public actionSheetCtrl: ActionSheetController, public appMin: AppMin, public loadingCtrl: LoadingController, ) {
    if (this.appMin.GetPhoneMode() != "ios") {

      this.header_class = "header_class_md";
    }
    else {
      this.header_class = "header_class";
    }


    let _this = this;


  }

  ionViewDidLoad() {
    // this.addApplicationConsultant();
  }
  ngOnInit() {
    this.addApplicationConsultant();
  }

  async send(value, isshow?: boolean) {
    if (isshow) {
      // this.loader = await this.loadingCtrl.create({
      //   header : "加载中...",
      //   duration: 3000
      // });
      // this.loader.present();
    }

    this.appMin.post(JSON.parse(value)).subscribe((data) => {
      this.dataHandle(data);
    });


  }
  async selectGender() {
    //选择性别
    let actionSheet = await this.actionSheetCtrl.create({

      buttons: [
        {
          text: '男',
          // role: 'destructive',
          handler: () => {
            this.input_gender = "男";
            this.input_sex = '0';
          }
        },
        {
          text: '女',
          handler: () => {
            this.input_gender = '女';
            this.input_sex = '1';
          }
        },
        {
          text: '取消',
          role: 'cancel',
          handler: () => {
          }
        }
      ]
    });

    actionSheet.present();
  }
  addApplicationConsultant() {
    let obj = {
      type: 'ApplicationConsultant',
      userId: this.appMin.getUserid()
    }
    this.send(JSON.stringify(obj));
  }
  async presentAlert(val) {
    let alert = await this.alertCtrl.create({
      // header: '提示',
      message: val,
      buttons: ['确定']
    });
    alert.present();
  }
  ApplicationConsultant() {
    //提交申请表
    let this1 = this;
    if (this.input_name == '') {
      this.presentAlert("请填写姓名");
      return;
    }
    // if (this.input_QQ.trim() == '') {
    //   this.presentAlert("请填写QQ号");
    //   return;
    // }
    if (this.input_workYears == '') {
      this.presentAlert("请填写从业时长");
      return;
    }
    if (this.input_age == '') {
      this.presentAlert("请填写年龄");
      return;
    }
    console.log('this.input_sex='+this.input_sex);
    if (this.input_sex == '') {
      this.presentAlert("请填写性别");
      return;
    }




    let obj = {
      type: 'upApplicationConsultant',
      userId: this.appMin.getUserid(),
      input_age: this.input_age,
      input_gender: (this.input_gender == '男') ? '0' : '1',
      input_name: this.input_name,
      input_otherPlatforms: this.input_otherPlatforms,
      input_QQ: this.input_QQ,
      input_workYears: this.input_workYears,

    }
    this.send(JSON.stringify(obj));

  }
  dataHandle(meg: any) {
    console.log(JSON.stringify(meg));
    if (meg['type'] == 'ApplicationConsultant_ret') {
      this.input_CName = meg['CName'];
      this.input_name = meg['name'];

      this.input_gender = meg['gender'];
      if (meg['gender'] == '') {
        this.checkGender = true;

       
        
      }else{
        if(meg['gender'] == '男')
        {this.input_sex='0';}
        else
        {this.input_sex='1';}
      }
      this.input_PhoneUnmber = meg['PhoneUnmber'];
    }
    if (meg['type'] == 'upApplicationConsultant_ret') {
      if (meg['count'] == '1') {
        //提交成功 准备跳转
        this.appMin.setIsApplicationConsultant(true); //标识已经申请了
        this.Router.navigate(['application-consultant2'])
        // this.navCtrl.push(ApplicationConsultant_2Page);
      } else {
      //  console.log('提交失败');
      }
    }


  }

}
